import React, { FC, ElementType } from 'react';
import { createUseStyles } from 'react-jss';
import clsx from 'clsx';

const useStyles = createUseStyles({
    button: {
        composes: 'flexRow center justifyCenter font-Montserrat-medium',
        // lineHeight: '1.25em',
        // letterSpacing: 4,
        // textTransform: 'uppercase',
        // webkitFontSmoothing: 'antialiased',
        // fontWeight: 'bold',
        boxShadow: '0 1px 4px rgba(26,26,67,.1)',
        borderRadius: 8,
        padding: '12px 16px',
    },
    link: {
        textDecoration: 'none',
    },
});

type Props = { text: string; className?: string; url?: string };
const Button: FC<Props> = ({ text, url, className }) => {
    const classes = useStyles();
    const Component = (url ? `a` : 'button') as ElementType;
    const otherProps = url ? { href: url } : {};
    return (
        <Component {...otherProps} className={clsx(classes.button, className, url ? classes.link : null)}>
            <span>{text}</span>
            <img src={`/images/icons/caret.svg`} alt="icon" className={classes.button}/>
        </Component>
    );
};

export default Button;
